/* 상단바 */
.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
}

/* 바 왼쪽 */
.n-left{
    flex: 3;
    align-items: center;
    display: flex;
    gap: 2rem;
}

/* 이름 */
.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}

/* 바 오른쪽 */
.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    flex : 10;
}

.n-list>ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
}

/* 메뉴에 마우스 갖다댈 시*/
.n-list>ul>li:hover{
    cursor: pointer;
    color: var(--yellow2);
}